.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 999;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__contents {
    display: flex;
    justify-content: space-between;
}

.nav__black {
    background-color: #111;
}

.navlink__logo{
    cursor: pointer;
}

.nav__logo{
    position: fixed;
    top: 19px;
    left: 0px;
    width: 80px;
    height: auto;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
    margin-right: 20px;
} 

.navlink__avatar{
    cursor: pointer;
}

.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    opacity: 0.8;
    border-radius: 5px;
}
